<template>
  <div :style='{height: ht + "px"}' style='position: relative; width: 100%;'>
    <img src="../assets/bg.png" style='width: 100%; height: 99.5%;'/>
    <div style='position: absolute; top: 0px; left: 0px; width: 100%; text-align: center;'>
      <van-icon @click='$router.push("/mine")' name="arrow-left" color='#fff' size='40'
                style='margin-top: 20px; margin-left: 10px; float: left;'/>
      <div style='background: #fff; border-radius: 10px; width: 90%; margin-top: 18%; height: auto; display: inline-block; box-sizing: border-box; padding: 30px 20px; text-align: left;'>
        <div class='inputBg'>
          <span>姓名</span>
          <input v-model='real_name' placeholder='请输入您的姓名'/>
        </div>
        <div class='inputBg'>
          <span>身份证</span>
          <input v-model='id_code' placeholder='请输入您的身份证'/>
        </div>
        <div class='inputBg'>
          <span>所属机关</span>
          <select v-model='ssjg' @change='ssjgChange'>
            <option v-for='item in ssjgList' :key='item.id' :value='item.id'>{{item.name}}</option>
          </select>
        </div>
        <div class='inputBg'>
          <span>所属部门</span>
          <select v-model='ssbm' @change='ssbmChange'>
            <option v-if='ssjg === "" '>请先选择所属机关</option>
            <option v-else v-for='item in ssbmList' :key='item.id' :value='item.id'>{{item.name}}</option>
          </select>
        </div>
        <div class='inputBg'>
          <span>员工岗位</span>
          <select v-model='yggw'>
            <option v-if='ssbm === "" '>请先选择所属部门</option>
            <option v-else v-for='item in yggwList' :key='item.id' :value='item.id'>{{item.name}}</option>
          </select>
        </div>
        <div @click='okQuery' style='width: 100%; margin-top: 20px; height: 52px; font-size: 22px; text-align: center;line-height: 52px; border-radius: 30px; color: #fff; background:linear-gradient(90deg,rgba(255,142,106,1) 0%,rgba(250,84,28,1) 100%);'>
          确定
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "wanshan",
    data() {
      return {
        ht: window.screen.height,
        ssjgList: [],
        ssbmList: [],
        yggwList: [],
        ssjg: '',
        ssbm: '',
        yggw: '',
        id_code: '',
        real_name: ''
      }
    },
    mounted() {
      this.getData()
    },
    methods: {
      async okQuery() {
          if (this.id_code === '' || this.real_name === '' || this.ssjg === '' || this.ssbm === '' || this.yggw === ''){
            this.$toast('请完善资料')
            return
          }
          if (this.yggwList.length !== 0 && this.yggw === ''){
              this.$toast('请选择员工岗位')
              return
          }
          if (this.id_code.length !== 18) {
              this.$toast('证件号格式错误')
              return
          }
          const {err, data} = await this.$service.index.perfecInfoPost({
              token: sessionStorage.getItem('token'),
              real_name: this.real_name,
              id_code: this.id_code,
              type_id: this.ssjg,
              type: this.yggw === '' ? this.ssbm : this.yggw
          })
          if (err) {
              this.$toast(err.message);
              return;
          }
          console.log(data)
      },
      async getData() {
        const {err, data} = await this.$service.index.perfecInfo()
        if (err) {
          this.$toast(err.message);
          return;
        }
        this.ssjgList = data.types
      },
      async ssjgChange() {
          const {err, data} = await this.$service.index.checktype({
              type: this.ssjg
          })
          if (err) {
              this.$toast(err.message);
              return;
          }
          this.ssbmList = data.types
      },
      async ssbmChange() {
          const {err, data} = await this.$service.index.checkpo({
              type_id: this.ssbm
          })
          if (err) {
              this.$toast(err.message);
              return;
          }
          this.yggwList = data.types
      }
    }
  }
</script>

<style scoped>
  .inputBg {
    width: 100%;
    height: 52px;
    margin-top: 15px;
    line-height: 52px;
    border-radius: 30px;
    background: rgba(247, 247, 247, 1);
  }

  .inputBg span {
    font-size: 16px;
    width: 80px;
    height: 25px;
    margin-right: 10px;
    margin-top: 13px;
    border-right: 1px solid rgba(233, 233, 233, 1);
    line-height: 25px;
    text-align: right;
    float: left;
  }

  .inputBg input, .inputBg select {
    width: 200px;
    float: left;
    height: 50px;
    background: none;
    color: rgba(191, 191, 191, 1);
    border: none;
    font-size: 18px;
  }
</style>